import React, { useState } from 'react';
import { Navbar, Nav, NavDropdown, Button, Jumbotron } from 'react-bootstrap';
import './App.css';
import Data from './data.js';
import Detail from './Detail.js';
import axios from 'axios';

import { Link, Route, Switch } from 'react-router-dom';

// import img1 from '../src/image1.jpg';
// import img2 from '../src/image2.jpg';
// import img3 from '../src/image3.jpg';

function App() {

  let [product, setProduct] = useState(Data);
  let [stock, setStock] = useState([10, 11, 12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">MoodwasH</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="W.I.P" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">2021</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">2020</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">2019</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">2018</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">2017</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">2016</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">2015</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">See all</NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title="STUDY" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <Switch>

        <Route exact path="/">
          <Jumbotron className="background">
            <h1>Let's MoodwasH!</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>

          <div className="container">
            <div className="row">
              {
                product.map( (data, i) => {
                  return <Card product={data} i={i} key={i}/>
                })
              }
            </div>
            <button className="btn btn-primary" onClick={() => {

              // 로딩중 UI 띄움

              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result) => {
                // 로딩중 UI 지움
                setProduct( [...product, ...result.data] );
              })
              .catch(() => {
                console.log('fail')
              })
            }}>more</button>
          </div>
        </Route>




        <Route path="/detail/:id">
          <Detail product={product} stock={stock} setStock={setStock} />
        </Route>

        <Route path="/:id">
          <div>아무거나 입력하지 마!</div>
        </Route>

      </Switch>
    </div>
  );
}


function Card(props) {
  return (
    <div className="col-md-4">
      <img src={'https://codingapple1.github.io/shop/shoes' + (props.i + 1) + '.jpg'} width="100%" />
      <h4> { props.product.title } </h4>
      <p> { props.product.content } </p>
      <p> { props.product.price } </p>
    </div>
  )
}

export default App;
